import * as THREE from 'three';

export class Layers3D {
  constructor() {
    // Initialize the scene, camera, and renderer
    this.scene = new THREE.Scene();
    this.camera = new THREE.PerspectiveCamera(75, 1.0, 0.1, 1000);
    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setSize(300, 300);

    this.canvasVG = document.getElementById("layers3D-container");
    this.canvasVG.appendChild(this.renderer.domElement);

    // Create a cube and add it to the scene
    const cubeGeometry = new THREE.BoxGeometry(1, 1, 1);
    const cubeMaterial = new THREE.MeshStandardMaterial({ color: 0x00ff00 });
    this.cube = new THREE.Mesh(cubeGeometry, cubeMaterial);
    this.scene.add(this.cube);

    // Set up lights
    const ambientLight = new THREE.AmbientLight(0x404040);
    this.scene.add(ambientLight);

    // const directionalLight = new THREE.DirectionalLight(0xffffff);
    // directionalLight.position.set(1, 1, 1).normalize();
    // this.scene.add(directionalLight);

    // Set camera position
    this.camera.position.z = 5;

    // Set background color
    this.renderer.setClearColor(0xFFE699);

    this.animate();
  }

  animate() {
    // Rotate the cube in the animate method
    this.cube.rotation.x += 0.01;
    this.cube.rotation.y += 0.01;

    // Render the scene
    this.renderer.render(this.scene, this.camera);

    // Request the next animation frame
    requestAnimationFrame(this.animate.bind(this));
  }
}



